  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-sizing: content-box;
          box-sizing: content-box;
  padding: 0px;
}
.home-aside {
  background-color: #ECECEC;
  width: 100%;
}
.home-main {
  background-color: #fff;
  color: #000;
  text-align: center;
  margin: 0px;
  padding: 0px;
}
.home_title {
  color: #fff;
  font-size: 22px;
  display: inline;
  margin-left: 8px;
}
.home_userinfo {
  color: #fff;
  cursor: pointer;
}
.home_userinfoContainer {
  display: inline;
  margin-right: 20px;
}
.el-submenu .el-menu-item {
  width: 180px;
  min-width: 175px;
}

.el-carousel__item h3[data-v-ce086d94] {
  color: #475669;
